import NextLogo from '../icons/check-solid.svg'
import SeenLogo from '../icons/check-double-solid.svg'

export const Instructions = (props) => {
    return (
        <div>
            <h1 class="instruction-title"> The rules are simple</h1>
            <ul class="instructions">
                <li> Press <img class="playing-buttons small" src={NextLogo} alt="next"/> if you are seeing the Pokemon for the <span> first time </span></li>
                <p> +1 Point </p>
                <li> Press  <img class="playing-buttons small" src={SeenLogo} alt="seen"/> if you have <span> already seen </span>that Pokemon</li>
                <p> +2 Point </p>
                <li> Any wrong guess will result in game over </li>
            </ul>
            <button class="play-button" onClick={props.handlePlayClick}> Play ! </button>
        </div>
    )
}