import NextLogo from '../icons/check-solid.svg'
import SeenLogo from '../icons/check-double-solid.svg'

export const Footer = (props) => {
    return (
        <div id="footer">
            <button onClick={props.handleCantFoolMeClick}> <img class="playing-buttons" src={SeenLogo} alt="seen"/> </button>
            <button onClick={props.handleFirstTimeClick}> <img class="playing-buttons" src={NextLogo} alt="next"/> </button>
        </div>
    );
}