import { Header } from "./components/Header.js";
import { Score } from "./components/Score.js";
import { Card } from "./components/Card.js";
import { Footer } from "./components/Footer.js";
import { GameOver } from "./components/GameOver.js";
import "./main.css";
import { Component } from "react";
import { Fragment } from "react";
import pokemonImages from "./data/pokemon-images.json";
import { Instructions } from "./components/Instructions.js";

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      play: false,
      pokemonIndex: Math.floor(Math.random()*50),
      score: 0,
      bestScore: 0,
      passed: new Set(),
      isGameOver: false
    }
  }

  preloadImages = () => {
    for(let image in pokemonImages){
      new Image().src = pokemonImages[image];
    }
  }

  handleFirstTimeClick = () => {
    const {passed, pokemonIndex} = this.state;
    if(passed.has(pokemonIndex)){
      this.gameOver();
    }else{
      this.addScoreandMoveForward(1);
    }
  }

  handleCantFoolMeClick = () =>{
    const {passed, pokemonIndex} = this.state;
    if(passed.has(pokemonIndex)){
      this.addScoreandMoveForward(2);
    }else{
      this.gameOver();
    }
  }

  handleResetClick = () =>{
    let newIndex = 0;
    while((newIndex=Math.floor(Math.random()*50))===this.state.pokemonIndex);
    this.setState({
      score: 0,
      pokemonIndex: newIndex,
      passed: new Set(),
      isGameOver: false
    });
  }

  handlePlayClick = () => {
    this.setState({play: true});
  }

  gameOver = () => {
    this.setState({isGameOver: true});
  }

  addScoreandMoveForward = (addPoints) => {
    const {pokemonIndex,score,bestScore,passed} = this.state;
    let newIndex = 0;
    while((newIndex=Math.floor(Math.random()*50))===this.state.pokemonIndex);
    this.setState({
      pokemonIndex: newIndex, 
      passed: passed.add(pokemonIndex),
      score: score+addPoints,
      bestScore: bestScore < score+addPoints ? score+addPoints : bestScore,
    });
  }

  componentDidMount = () => {
    this.preloadImages();
  }

  render(){
    const {pokemonIndex, score, bestScore, isGameOver, play} = this.state;
    let element;
    if(!play && !isGameOver){
      element=<Instructions handlePlayClick={this.handlePlayClick}/>
    }else if(isGameOver){
      element = 
      <Fragment>
        <Score score={score} bestScore={bestScore}/>
        <GameOver handleResetClick={this.handleResetClick} />
      </Fragment>
    }else{
      element = 
        <Fragment>
          <Score score={score} bestScore={bestScore}/>
          <Card pokemonIndex={pokemonIndex} />
          <Footer handleFirstTimeClick={this.handleFirstTimeClick} handleCantFoolMeClick={this.handleCantFoolMeClick}/>
        </Fragment>
    }
    return (
      <div id="container">
        <Header/>
        {element}
      </div>
    );
  }
}

export default App;